

const Accordion = ({children, title}) => {
    let accordionOpen = false;
    return (
        <div className={'cl-accordion'+ ' '+accordionOpen?'open':''} onClick = {console.log("clicked") }>
            <div className="accordion-head">
                <h1>{title}</h1>
            </div>
            {children}
        </div>
    )
}

export default Accordion;

